.products {
  background-image: url(../images/twoeero.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  max-width: 1440px;
  height: 1179px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 188px;
  padding-right: 188px;
  padding-top: 580px;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    & {
      background-image: url(../images/twoeero@2x.jpg);
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 89px;
  }

  &__item {
    flex-basis: 424px;
    color: $colorTextWhite;
  }

  &__title {
    font-size: 36px;
    line-height: 1.22;
    font-weight: 400;
    margin-bottom: 13px;

    &::before {
      display: block;
      content: '';
      width: 81px;
      height: 21px;
      background-color: $colorBackgroundPrimary;

      margin-bottom: 16px;
      border-radius: 18px;
    }
  }

  &__holder {
    position: relative;
  }

  &__caption {
    position: absolute;
    left: 14px;
    top: 0;
    color: $colorBlack;
    font-weight: 700;
    line-height: 1.31;
  }

  &__subtitle {
    width: 222px;
    font-size: 42px;
    line-height: 1.14;
    font-weight: 400;
    margin-bottom: 20px;
  }

  &__wrapper {
    position: relative;
    height: 96px;
    margin-bottom: 20px;
  }

  &__subtitle--wide {
    position: absolute;
    top: 0;
    left: 0;
    width: 542px;
  }

  &__description {
    font-size: 18px;
    line-height: 1.4;
    margin-bottom: 33px;

    &:last-child {
      margin-bottom: 36px;
    }
  }

  &__price {
    font-weight: 700;
  }

  &__button {
    background-color: $colorAccent;
    color: $colorTextWhite;
    width: 220px;
    height: 52px;
    padding-top: 17px;
    padding-bottom: 21px;

    font-size: 14px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.11px;
    text-transform: uppercase;
    text-align: center;
    border: none;

    transition: background-color $baseTransition;

    &:hover,
    &:focus {
      background-color: $colorButtonHover;
    }
  }

  &__box {
    text-align: center;
  }

  &__link {
    position: relative;
    color: $colorTextWhite;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.14;

    &::after {
      position: absolute;
      bottom: -6px;
      left: 50%;
      transform: translateX(-50%);

      content: '';
      background-color: $colorTextWhite;
      height: 1px;
      width: 154px;
    }
  }
}
